@font-face {
    font-family: "Helvetica-Now";
    src: url("../font/HelveticaNowText-Light.otf") format('opentype');
}

@media only screen and (max-width: 600px) {
    .name-wooseongkim {
        font-size : 21.5px !important;
        line-height : 1.5em;
        padding-bottom : 100px !important;
    }

    .nav_bar {
        padding-bottom : 12px;
        width : 50% !important;
    }

    .text-project-name {
        font-size : 16px !important;
    }

    .text-project-info {
        font-size : 13px !important;
    }

    .nav-grid-container {
        padding-top : 6.5em !important;
    }
    
    .item-grid-container {
        padding-top : 6.5em !important;
    }

    .text-info-wooseongkim {
        font-size : 13px;
        line-height : 1.5em;
    }

    .nav_content {
        font-size : 15px;
    }

    .text-copyright-wooseongkim {
        font-size : 11px;
        color : #5e5e5e;
    }
    
    .link-wooseongkim {
        font-size : 11px;
        color : #5e5e5e;
    }
}

@media only screen and (min-width: 600px) {
    .name-wooseongkim {
        font-size : 20px;
        line-height : 1.5em;
    }
    
    .nav-grid-container {
        padding-top : 2em !important;
        position : fixed;
    }

    .item-grid-container {
        padding-top : 20em;
    }

    .text-info-wooseongkim {
        font-size : 1.25em;
        line-height : 1.5em;
    }

    .nav_content {
        font-size : 16px;
    }

    .text-copyright-wooseongkim {
        font-size : 13px;
        color : #5e5e5e;
    }
}

html {
    overflow-y: scroll;
    background-color: #FAFAFA;
}
 
