    width : 100%;
}

.text-project-name {
    padding-top : 10px;
    font-size : 18px;
}

.text-project-info {
    font-size : 13.5px;
}

.MuiGrid-item {
    padding-top : 0px !important;
}

.frame-item {
    padding-bottom : 50px;
}

.hover-area:hover .master-image{
    opacity: 0;

    transition: opacity .5s, visibility .5s;
}

a:link    {color:#000;}  /* unvisited link  */
a:visited {color:#000;}  /* visited link    */
a:hover   {color:#000;}  /* mouse over link */
a:active  {color:#000;}  /* selected link   */ 

.master-part {
    position : relative;
    align-items: center;
    display: flex;
    justify-content: center;
    padding: auto;
}

.master-part div {
    position: absolute;
    margin: auto;
    text-align: center;
    /* background: #F3F3F3; */
    font-size: 25px;
    color: black;
    visibility: hidden;
    opacity: 0;
  
    /* transition effect. not necessary */
    transition: opacity 0.5s, visibility 0.5s;
  }
  
  .hover-area:hover .project-question {
    visibility: visible;
    opacity: 1;
  }

  .bg-gray {
      position : absolute;
  }

  .master-image {
      position : relative;
  }